.form-container{
    width: 50%;
    padding: 0 7rem;
   
}
.form-contents{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}
.form-text{
    padding: 0.5rem;
    outline: none;
    border: none;
    background-color: var(--ins-light);
    resize: none;

}
.form-text:cl{
   border-bottom: 1px solid var(--ins-primary);

}
.form-heading{
    margin: 2rem 0;
}
.submit-button{
    margin: 1rem 0;
}